.c-pt {
  padding: 4rem 2rem;
  .js-table-wrap {
    transition: max-height 500ms ease;
    overflow: hidden;
    @include bp(md) {
      transition: max-height 1000ms ease;
    }

    &.closed {
      max-height: 0;
    }

    &.open {
      max-height: var(--table-height);
    }
  }

  &__table {
    display: grid;
    border-radius: 0.4rem;
    padding: 2rem;
  }

  &__thead,
  &__tbody,
  &__tr {
    display: grid;
    grid-template-columns: 50% repeat(var(--grid-cols), minmax(0, 1fr));
  }

  &__tr,
  &__tbody,
  &__group-heading,
  &__feature-wrap {
    grid-column: 1 / span end;
  }

  &__th,
  &__td {
    padding: 1rem;
  }

  &__group-heading {
    border-radius: 0.4rem;
    padding: 1rem;
    margin: 0;
    & > h4 {
      margin: 0;
    }

    & > .i-chevron-down {
      line-height: 0;
      transition: transform 230ms ease-in-out;
    }

    &.open .i-chevron-down {
      transform: rotate(0deg);
    }
    &.closed .i-chevron-down {
      transform: rotate(180deg);
    }
  }

  &__feature-wrap {
    @include bp-max(md) {
      transition: max-height 430ms ease-in-out;
      overflow: hidden;
      &.closed {
        max-height: 0rem;
      }
      &.open {
        max-height: var(--expand-to-height);
      }
    }
  }

  &__group &__feature:not(:last-child) {
    border-bottom: 1px solid color('grey', 2);
  }

  &__group:not(:last-child) {
    margin-bottom: 1rem;
    @include bp(md) {
      margin-bottom: 0rem;
    }
  }

  .i-check {
    height: 2rem;
    width: 2rem;
  }

  &__pill {
    padding: 0.2rem 0.8rem;
    border-radius: 3.2rem;
    white-space: nowrap;
  }

  &__expand-wrapper {
    padding: 2rem;
    scroll-padding-top: 5.858rem;
    @include bp(md) {
      scroll-padding-top: 7.6rem;
    }
  }

  .c-btn {
    cursor: pointer;
    line-height: 0;

    span {
      line-height: 1;
    }
    .i-chevron-down {
      margin-left: 1rem;
      transition: transform 230ms ease-in-out;
      transform-origin: center center;
    }
    &.open {
      .i-chevron-down {
        transform: rotate(180deg);
      }
      .show-text {
        display: none;
        visibility: hidden;
      }
      .hide-text {
        display: inline;
        visibility: visible;
      }
    }
    &.closed {
      .i-chevron-down {
        transform: rotate(0deg);
      }
      .hide-text {
        display: none;
        visibility: hidden;
      }
      .show-text {
        display: inline;
        visibility: visible;
      }
    }
  }
}
