@include bp(md) {
  .u-watch {
    .c-testimonial {
      opacity: 0;
      transform: translateY(2rem);
      will-change: transform opacity;
    }
    &--active .c-testimonial {
      opacity: 1;
      transform: translateY(0rem);
    }
  }
}
.c-testimonial--outer {
  padding: 2rem;
  height: fit-content;
  width: 100%;
  @include bp(md) {
    padding: 0rem;
  }
}

.c-testimonial {
  border-radius: 0.4rem;
  padding: 2rem;
  box-shadow: 0px 3px 8px #3f4f5f15;
  height: fit-content;
  width: 100%;
  transition: none;
  @include bp(md) {
    transition: opacity 680ms ease-in-out var(--delay),
      transform 680ms ease-in-out var(--delay);
  }
  .c-by-line {
    .f-text {
      margin-bottom: 0rem;
    }
    .f-text--caption {
      margin-top: 0rem;
    }
    .c-ar-img {
      max-width: 5.6rem;
    }
  }

  &__mark {
    position: absolute;
    height: 2rem;
    width: 2rem;
    top: 0;
    left: -3rem;
  }
}
