.c-link-listing {
  padding: 3rem 2rem;
  ul#{&}__list {
    margin: 2rem 0 4rem;
    & > li:before {
      display: none;
    }
    li {
      > span {
        display: block;
      }
      > a {
        @include bp(md) {
          font-size: 1.8rem;
        }
      }
    }
    li:not(:last-child) {
      margin-bottom: 3rem;
    }
  }
}
