.c-icon-grid {
  padding: 6rem 2rem;
  h2 {
    margin-top: 0;
    margin-bottom: 4rem;
  }
  &__inner {
    display: grid;
    gap: 4rem;
    grid-template-columns: 1fr;
    @include bp(md) {
      &--1x {
        grid-template-columns: 1fr;
      }

      &--2x {
        grid-template-columns: 1fr 1fr;
      }

      &--3x {
        grid-template-columns: 1fr 1fr 1fr;
      }
    }
  }

  &__cta {
    margin-top: 2rem;
    display: inline-block;
  }
}
