.header {
  padding: 1.6rem 0;
  z-index: 99999;
  .c-logo {
    padding-right: 2rem;
    display: inline-flex;
  }
  &::before {
    content: '';
    display: block;
    position: absolute;
    width: 100%;
    height: 100%;
    box-shadow: 0rem 0.4rem 0.8rem #3f4f5f14;
    opacity: 0;
    top: 0;
    pointer-events: none;
  }
  &.js-sticky::before,
  &.open::before {
    opacity: 1;
    z-index: 1;
  }
}

.header {
  .nav-btns {
    margin: 4rem 0;
    @include bp(md) {
      margin: 0 -0.5rem;
      padding: 0;
      flex-wrap: nowrap;
      a {
        white-space: nowrap;
      }
    }
    .c-link {
      margin-left: 2.6rem;
      @include bp(md) {
        margin-left: 0;
        margin-right: 2.6rem;
      }
      &:hover {
        color: color('cta-blue');
      }
    }
  }
}

.hamburger {
  height: 2.2rem;
  width: 2.2rem;
  min-height: 2.2rem;
  min-width: 2.2rem;
  position: relative;
  background-color: color('transparent');
  cursor: pointer;
  &__line {
    position: absolute;
    width: 100%;
    height: 0.4rem;
    border-radius: 0.4rem;
    opacity: 1;
    transform-origin: center center;
    transform: translate(0, -50%) rotate(0deg);
    background-color: color('cta-blue');
    left: 0;
    transition: transform 230ms ease-in-out, opacity 230ms ease-in-out,
      top 230ms ease-in-out;
    &--top {
      top: 0.2rem;
    }
    &--middle {
      top: 50%;
    }
    &--bottom {
      top: calc(100% - 0.2rem);
    }
  }
  &.open &__line {
    &--top {
      transform: translate(0, -50%) rotate(45deg);
      top: 50%;
    }
    &--middle {
      opacity: 0;
    }
    &--bottom {
      top: 50%;
      transform: translate(0, -50%) rotate(-45deg);
    }
  }

  &:focus {
    outline: 0px solid transparent;
  }
}
