.c-headshot {
  $self: &;
  &__img {
    overflow: hidden;
    border-radius: 0.4rem;
    &--rollover {
      position: absolute;
      top: 0;
      left: 0;
      opacity: 0;
      width: 100%;
      transition: opacity 230ms ease-in-out;
      &:after {
        content: '';
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        display: inline-block;
        background-color: var(--bg-color);
        opacity: 0.7;
      }
    }

    &--content {
      display: flex;
      flex-flow: column;
      justify-content: flex-end;
      text-align: center;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      padding: 2rem;
      opacity: 0;
      transition: opacity 230ms ease-in-out;

      .f-text--caption {
        color: color('white');
      }

      &__links {
        margin: 0 -1.5rem;
        a,
        a:visited {
          color: color('cta-light-blue');
          margin: 0 1.5rem;
          svg {
            height: 2rem;
            width: 2rem;
          }
        }
      }
    }
  }

  &__name {
    text-align: center;
    margin-bottom: 0;
  }
  &__designation {
    width: 100%;
    text-align: center;
    color: color('sky');
    margin: 0;
  }

  &:hover {
    #{$self}__img {
      &--rollover,
      &--content {
        opacity: 1;
      }
    }
  }
}
