.o-blog {
  &__share {
    a {
      background-color: color('grey', 1);
      color: color('grey', 3);
      line-height: 0;
      background-size: 0;
      font-size: initial;
      padding: 0.35rem;
      border-radius: 0.4rem;
      display: inline-block;
      &:not(:last-child) {
        margin-right: 1rem;
      }
      svg {
        height: 2.4rem;
        width: 2.4rem;
        vertical-align: middle;
        line-height: 0;
      }
    }
  }
}
.o-blog--wrapper {
  position: relative;
}
.o-blog--content {
  display: inline-block;
  padding: 0 2rem 2rem;
  border-radius: 0.4rem;
  @include bp(md) {
    margin-top: -10rem;
  }

  .u-mobile-only {
    .c-by-line {
      width: 100%;
      padding-bottom: 3rem;
      margin-bottom: 2rem;
      border-bottom: 0.2rem solid color('grey', 2);
    }
  }

  ol,
  ul {
    width: 100%;
    display: inline-block;
    li {
      margin: 2rem 0;
      &:first-child {
        margin-top: 0;
      }
      &:last-child {
        margin-bottom: 0;
      }
    }
  }

  .c-audio,
  .f-text--pq,
  .f-text--bq,
  .c-caption-img,
  .c-promo {
    padding: 4rem 0;
  }

  .c-testimonial {
    margin: 4rem 0;
  }

  // this scroll margin should keep headings from
  // getting obscured by the header when the ToC is used
  h2 {
    scroll-margin-top: 5.858rem;
    @include bp(md) {
      scroll-margin-top: 10rem;
    }
  }
}

.single-blog {
  .c-ar-img__32-11 {
    margin: auto -2rem;
    @include bp(md) {
      overflow: hidden;
      border-radius: 0.4rem;
    }
    @include bp(md) {
      margin: auto;
    }
  }
}
