.c-tooltip {
  margin-left: 0.5rem;
  vertical-align: middle;
  position: relative;
  line-height: 0;
  display: inline-block;
  & .right {
    background-color: color('grey', 1);
    color: color('grey', 1);
    min-width: 25rem;
    top: 50%;
    left: 100%;
    margin-left: 1.2rem;
    transform: translate(0, -50%);
    padding: 1rem 2rem;
    color: #9ea3a8;
    background-color: #e4eaf0;
    font-weight: normal;
    border-radius: 0.4rem;
    position: absolute;
    z-index: 99999999;
    display: none;
    box-sizing: border-box;
    line-height: 1.5;
    text-align: left;
  }

  & > a {
    color: #b6c7d4;
    position: relative;
    cursor: default;
    &:hover,
    &:focus {
      background-size: 0;
    }
  }

  & .right i {
    position: absolute;
    top: 50%;
    right: 100%;
    margin-top: -1.2rem;
    width: 1.2rem;
    height: 2.4rem;
    overflow: hidden;
  }

  & .right i::after {
    content: '';
    background-color: color('grey', 1);
    position: absolute;
    width: 1.2rem;
    height: 1.2rem;
    left: 0;
    top: 50%;
    transform: translate(50%, -50%) rotate(-45deg);
    background-color: #e4eaf0;
  }

  &:hover,
  &:focus {
    background-size: 0;
    & .right {
      display: block;
    }
  }
}
