@use "sass:math";
/**
* 16:9     -- basic ratio used most places
*/

.c-ar-vid {
  @each $ar, $w, $h in $aspect_ratios {
    &__#{$ar} {
      display: inline-block;
      line-height: 0;
      background-color: color('grey', 2);
      position: relative;
      &::before {
        content: '';
        width: 1px;
        margin-left: -1px;
        float: left;
        height: 0;
        padding-top: math.div($h, $w) * 100%;
      }
      &::after {
        content: '';
        display: table;
        clear: both;
      }
      video {
        width: 100%;
        object-fit: cover;
      }
    }
  }
}
