a.c-btn,
input.c-btn,
button.c-btn,
div.c-btn a {
  display: inline-flex;
  padding: 1.4rem;
  font-weight: 500;
  color: color('white');
  border-radius: 0.4rem;
  line-height: 1;
  transition: background-color 230ms ease-in-out, color 230ms ease-in-out;
  animation: none;
  background-size: 0;
  &:visited {
    color: color('white');
  }
  &:hover {
    animation: none;
    background-size: 0;
  }
  &--sky {
    background-color: color('sky');
    &:hover {
      background-color: lighten(color('sky'), 20%);
      color: color('white');
    }
  }

  &--blue {
    background-color: color('cta-blue');
    &:hover {
      background-color: lighten(color('cta-blue'), 20%);
      color: color('white');
    }
  }

  &--green {
    background-color: color('cta-green');
    &:hover {
      background-color: lighten(color('cta-green'), 20%);
      color: color('white');
    }
  }
  &--clear {
    color: color('cta-blue');
    border: 0.2rem solid color('cta-blue');
    background-color: color('transparent');
    &:visited {
      color: color('cta-blue');
    }
    &:hover {
      background-color: color('cta-blue');
      color: color('white');
      &:visited {
        color: color('white');
      }
    }
  }

  &--no-btn {
    color: color('grey', 5);
    background-color: color('transparent');
    &:hover {
      color: color('cta-blue');
    }
    &:visited {
      color: color('grey', 5);
    }
  }
}

div.c-btn {
  &--sky a {
    @extend .c-btn--sky;
  }

  &--blue a {
    @extend .c-btn--blue;
  }

  &--clear a {
    @extend .c-btn--clear;
  }
  &--no-btn a {
    @extend .c-btn--no-btn;
  }
}
