@keyframes float {
  0% {
    transform: translateY(0);
  }
  25% {
    transform: translateY(1rem);
  }
  100% {
    transform: translateY(0);
  }
}

@keyframes underlineIn {
  100% {
    background-size: 100% 100%;
  }
}

@keyframes underlineOut {
  0% {
    background-size: 100% 100%;
  }
  100% {
    background-size: 0% 100%;
  }
}
