.c-by-line {
  display: inline-block;

  .c-by-line__inner {
    flex-wrap: nowrap;
  }

  .f-text--caption {
    margin-bottom: 0;
  }

  p {
    display: block;
  }

  a {
    display: inline-block;
    font-size: 1.4rem;
  }

  .c-ar-img {
    flex: 0 1 auto;
    max-width: 5.6rem;
  }

  figcaption {
    flex: 0 1 auto;
    margin-left: 2rem;
  }
}
