.c-blog-teaser {
  padding: 2rem 0rem;

  &__img {
    flex: 0 0 17.6rem;
    overflow: hidden;
    border-radius: 0.4rem;
  }
  a &__title {
    transition: color 230ms ease-in-out;
  }
  a:hover,
  a:focus {
    background-size: 0;
  }

  a:hover &__title {
    color: lighten(color('navy'), 20%);
  }

  &__content {
    padding-left: 2rem;

    h5 {
      margin: 0.5rem 0;
    }

    p {
      margin: 0;
    }
  }
}
