.c-p-switch {
  display: grid;
  grid-template-columns: min-content min-content min-content;
  gap: 1rem;
  align-items: center;
  grid-template-areas:
    'off switch on'
    'tag   tag    tag';
  position: relative;
  padding-bottom: 1.8rem;
  .c-switch {
    grid-area: switch;
  }

  &__label {
    &-off {
      grid-area: off;
    }
    &-on {
      grid-area: on;
    }
  }

  &__tag {
    grid-area: tag;
    display: none;
    visibility: hidden;
    opacity: 0;
    white-space: nowrap;
    position: absolute;
    left: 50%;
    transform: translate(-50%, 0%);
    &--off {
      display: block;
      visibility: visible;
      opacity: 1;
    }
  }

  &--checked &__label {
    &-on {
      font-weight: 500;
      color: color('sky');
    }
  }

  &--checked &__tag {
    &--off {
      display: none;
      visibility: hidden;
      opacity: 0;
    }
    &--on {
      display: block;
      visibility: visible;
      opacity: 1;
    }
  }
}
