/********************
** Breakpoints
********************/

$bp-small: 48em;
$bp-medium: 64em;
$bp-large: 75em;

/********************
** Media Queries
********************/

$mq-small: 'only screen and (min-width: #{$bp-small})';
$mq-medium: 'only screen and (min-width: #{$bp-medium})';
$mq-large: 'only screen and (min-width: #{$bp-large})';

/********************
** Fonts
********************/

$baseFontSize: 16;
$baseLineHeight: 1.8;

$f-base: 'Roboto', 'Helvetica', Arial, Lucida Grande, sans-serif;

$f-text: $f-base;
$f-title: 'Poppins', Helvetica Neue, Helvetica, Arial, Lucida Grande, sans-serif;
$f-icons: 'icons';

/********************
** Colors
********************/

//primary brand
$c-sky: #00bbf0;
$c-midnight: #00204a;
$c-navy: #093470;

//primary UI
$c-cta-blue: #115fce;
$c-cta-green: #55cc81;
$c-cta-light-blue: #c2d9fa;

//greyscale
$c-white: #ffffff;
$c-grey-1: #eff4f8;
$c-grey-2: #e2eaf0;
$c-grey-3: #b6c7d4;
$c-grey-4: #91a3b0;
$c-grey-5: #5b6b7c;
$c-grey-6: #3f4f5f;
$c-grey-7: #202d3a;
$c-grey-8: #0f1d2b;

//secondary palette
$c-green-1: #25c191;
$c-green-2: #087f51;
$c-green-3: #2be28a;
$c-green-4: #0f9665;

$c-blue-1: #2e97cc;
$c-blue-2: #014c63;
$c-blue-3: #4bbdea;
$c-blue-4: #186f9b;

$c-purple-1: #645ba5;
$c-purple-2: #251b68;
$c-purple-3: #7d77c9;
$c-purple-4: #584a87;

$c-magenta-1: #ea2a74;
$c-magenta-2: #9e154d;
$c-magenta-3: #e25495;
$c-magenta-4: #ce255d;

$c-orange-1: #ff9c4a;
$c-orange-2: #c65500;
$c-orange-3: #fcb17e;
$c-orange-4: #ff7b00;

$c-transparent: rgba(0, 0, 0, 0);

$c-map: (
  'sky': $c-sky,
  'midnight': $c-midnight,
  'navy': $c-navy,
  'white': $c-white,
  'transparent': $c-transparent,
  'cta-blue': $c-cta-blue,
  'cta-green': $c-cta-green,
  'cta-light-blue': $c-cta-light-blue,
  'grey': (
    1: $c-grey-1,
    2: $c-grey-2,
    3: $c-grey-3,
    4: $c-grey-4,
    5: $c-grey-5,
    6: $c-grey-6,
    7: $c-grey-7,
    8: $c-grey-8,
  ),
  'green': (
    1: $c-green-1,
    2: $c-green-2,
    3: $c-green-3,
    4: $c-green-4,
  ),
  'blue': (
    1: $c-blue-1,
    2: $c-blue-2,
    3: $c-blue-3,
    4: $c-blue-4,
  ),
  'purple': (
    1: $c-purple-1,
    2: $c-purple-2,
    3: $c-purple-3,
    4: $c-purple-4,
  ),
  'magenta': (
    1: $c-magenta-1,
    2: $c-magenta-2,
    3: $c-magenta-3,
    4: $c-magenta-4,
  ),
  'orange': (
    1: $c-orange-1,
    2: $c-orange-2,
    3: $c-orange-3,
    4: $c-orange-4,
  ),
);

/********************
** Paths
********************/

$p-images: '../img/';

/********************
** Transition Timing Functions
********************/
$cubic-bezier: cubic-bezier(0.55, 0.085, 0, 0.99);

/********************
** sizes
********************/

$sizes: 'xs' 'sm' 'md' 'lg' 'xl' 'xxl';

/********************
** spacing
********************/

$base-space: 5;
