.c-promo {
  max-width: 80.8rem;
  padding: 2rem;
  .c-cta {
    width: 100%;
  }
  &--inner {
    margin: 0;
    overflow: hidden;
    border-radius: 0.4rem;
    display: flex;
    flex-direction: column;
    @include bp(sm) {
      flex-direction: row;
      flex-wrap: nowrap;
    }
    .c-block-outer {
      text-align: left;
      @include bp(sm) {
        flex: 1;
        flex-basis: 55%;
        align-self: center;
      }
    }
    .c-ar-img {
      @include bp-max(sm) {
        &:before {
          padding-top: 0;
        }
      }
      @include bp(sm) {
        flex: 0.8;
        min-height: 100%;
        flex-basis: 45%;
        img {
          position: absolute;
          top: 50%;
          left: 0;
          transform: translate(0, -50%);
          height: 100%;
          width: auto;
          max-width: initial;
        }
      }
    }
  }
}
