.c-acc {
  padding: 4rem 0;
  & .row {
    margin: 0;
  }

  &__item-title {
    border-radius: 0.4rem;
    margin: 0;
    cursor: pointer;
    transition: color 230ms ease-in-out;
    &:hover {
      color: color('cta-blue');
    }
    & > p {
      margin: 0;
      width: calc(100% - 6rem);
      @include font-size(18);
    }
    & > span {
      line-height: 0;
      transition: transform 230ms ease-in-out;
      padding: 2rem;
    }
  }

  &__item.open &__item-title {
    color: color('cta-blue');
  }

  &__item {
    padding-bottom: 1rem;
  }
  &__item-content {
    overflow: hidden;
    transition: max-height 230ms ease-in-out;
  }

  &__item.open &__item-content {
    //TODO: make this better with JS
    max-height: var(--expand-to-height);
  }

  &__item.closed &__item-content {
    max-height: 0rem;
  }

  &__item.open &__item-title {
    span {
      transform: rotate(180deg);
    }
  }

  &__item.closed &__item-title {
    span {
      transform: rotate(0deg);
    }
  }

  &__item:not(:last-child) {
    border-bottom: 1px solid color('grey', 2);
  }
}
