.c-pinned-post {
  padding: 2rem 1rem;

  // a:hover,
  // a:focus {
  //   background-size: 0;
  // }

  a &__title {
    transition: color 230ms ease-in-out;
  }

  a:hover &__title {
    color: lighten(color('navy'), 20%);
  }
  &__content {
    padding-left: 2rem;

    h5 {
      margin-top: 0;
    }

    p {
      margin: 0;
    }
  }

  &__img {
    flex: 0 0 8.8rem;
    & img {
      position: absolute;
      min-width: 16rem;
      min-height: 9rem;
      height: 100%;
      width: auto;
      top: 50%;
      left: 0;
      transform: translateY(-50%);
    }
  }
}
