.c-p-co {
  &__inner {
    padding: 4rem 2rem;
    display: grid;
    grid-template-columns: minmax(0px, 1fr);
    @include bp(md) {
      grid-template-columns: repeat(3, minmax(0px, 1fr));
    }
    &--3x {
      @include bp(md) {
        grid-template-columns: repeat(3, minmax(0px, 1fr));
        .c-p-co__switch {
          margin: 1rem auto;
        }
        .c-p-co__content {
          grid-row: 1;
          grid-column: 2;
          text-align: center;
        }
        .c-p-co__card {
          grid-row: 2;
        }
      }
    }
  }

  &__switch {
    margin: 1rem auto;
    align-self: center;
    @include bp(md) {
      margin: 1rem 3rem;
      align-self: flex-start;
    }
  }

  &__content {
    display: flex;
    flex-flow: column;
    align-self: center;
    text-align: center;
    @include bp(md) {
      text-align: left;
    }
  }
}
