@use "sass:math";
/**
* 16:9     -- basic ratio used most places
* 16:7     -- ratio used for 2-col and 4-col grids
* 32:11    -- blog header image
* 332:253  -- Promo background image
* 1:1      -- square, used for icons, 
              testimonials and bylnes
* 67:84    -- headshots -- company page
* 5:2      -- group shot -- company page
*/

$aspect_ratios: (
  '16-9' 16 9,
  '16-7' 16 7,
  '32-11' 32 11,
  '332-253' 332 253,
  '1-1' 1 1,
  '67-84' 67 84,
  '5-2' 5 2
);

.c-ar-img {
  @each $ar, $w, $h in $aspect_ratios {
    &__#{$ar} {
      // background-color: color('grey', 2);
      position: relative;
      &::before {
        content: '';
        width: 1px;
        margin-left: -1px;
        float: left;
        height: 0;
        padding-top: math.div($h, $w) * 100%;
      }
      &::after {
        content: '';
        display: table;
        clear: both;
      }
      img {
        width: 100%;
        // object-fit: cover;
      }
    }
  }
}

//crops
.u-crop--c {
  overflow: hidden;
  border-radius: 100%;
}

.u-crop--sq {
  overflow: hidden;
}

.u-rc {
  overflow: hidden;
  border-radius: 0.4rem;
}
