$preheader-height: 4.8rem;
.c-pc {
  padding: 2rem;

  &__preheader {
    padding: 1rem;
    text-align: center;
    width: 100%;
    height: $preheader-height;
    border-top-left-radius: 0.4rem;
    border-top-right-radius: 0.4rem;
    @include bp(md) {
      top: 0;
      position: absolute;
    }
  }

  &__inner {
    border-radius: 0.4rem;
    box-shadow: 0px 0.3px 0.8px #3f4f5f14;
    @include bp(md) {
      padding-top: $preheader-height;
    }
  }

  &__price-wrapper {
    padding: 2rem 3rem;
    height: 22.7rem;
    display: flex;
    flex-flow: column nowrap;
    align-items: center;
    &.rounded {
      border-radius: 0.4rem 0.4rem 0 0;
    }
  }
  &__price {
    span.f-title--md {
      display: inline;
    }

    .js-price {
      display: none;
      visibility: hidden;

      &--show {
        display: block;
        visibility: visible;
      }
    }
  }

  &__price-desc {
    margin-top: 1rem;
  }

  &__cta {
    padding: 2rem 0 1rem;
    &.no-features {
      padding: 2rem 0 4rem;
    }
  }

  &__discount {
    position: absolute;
    opacity: 1;
    transition: opacity 230ms ease-in-out;
    color: white;
    margin-bottom: 1rem;
    padding: 0 1rem;
    z-index: 1;
    border-radius: 100%;
    right: 0;
    top: 0;
    font-weight: 600;
    font-family: $f-title;
    text-align: center;
    transform: translate(80%, 5%) rotate(15deg);
    svg {
      position: absolute;
      height: 100%;
      width: 100%;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }
  }

  .js-discount--hide {
    opacity: 0;
    visibility: hidden;
  }

  &--free &__discount,
  &--pro &__discount {
    opacity: 0;
    visibility: hidden;
  }

  &__features {
    text-align: center;
    padding: 0 3rem;
    ul {
      width: 100%;
      margin: 0;
      li {
        padding: 0.5rem 0;
        margin: 0.5rem 0;
        & h2,
        & h3,
        & h4,
        & h5 {
          margin: 0;
          line-height: 2;
        }
      }
      li:before {
        display: none;
      }
      li:not(:last-child) {
        border-bottom: 0.1rem solid color('grey', 2);
      }
    }
  }

  &__icon {
    width: 5.4rem;
  }

  &__input {
    flex-wrap: nowrap;
    > div {
      flex: 0 1 33%;
      &:first-child {
        text-align: right;
      }
      &:last-child {
        text-align: left;
      }
    }
    &--wrap {
      margin: 0 0.5rem;
    }
    input {
      padding: 0.2rem;
      text-align: center;
      width: 100%;
      border-radius: 0.4rem;
    }
  }
}
