//TO DO make color classes from scss map of color vars
@each $name, $color in $c-map {
  //color classes
  @if type-of($color) == 'map' {
    @each $shade, $color in $color {
      //color classes
      .u-c-#{$name}-#{$shade} {
        color: $color;
      }
      a.u-c-#{$name}-#{$shade} {
        &:visited {
          color: $color;
        }
        &:hover,
        &:focus {
          color: lighten($color, 20%);
        }
      }
      //bg classes
      ._u-c-#{$name}-#{$shade} {
        background-color: $color;
      }

      //fill classes
      .u-fill-#{$name}-#{$shade} {
        fill: $color;
      }
    }
  } @else {
    //color classes
    .u-c-#{$name} {
      color: $color;
    }

    a.u-c-#{$name} {
      &:visited {
        color: $color;
      }
      &:hover,
      &:focus {
        color: lighten($color, 20%);
      }
    }

    //bg classes
    ._u-c-#{$name} {
      background-color: $color;
    }

    //fill classes
    .u-fill-#{$name} {
      fill: $color;
    }
  }
}
