.c-featured-post {
  margin: 0 -2rem;
  margin-bottom: -4rem;
  @include bp(md) {
    margin: initial;
    padding: 2rem;
  }
  .c-featured-post__title {
    margin-top: 0.5rem;
  }
  a .c-featured-post__title {
    transition: color 230ms ease-in-out;
  }

  a:hover .c-featured-post__title {
    color: lighten(color('navy'), 20%);
  }

  .c-ar-img {
    border-radius: 0.4rem;
    overflow: hidden;
  }
}

.c-featured-post__content {
  position: relative;
  padding: 2rem;
  bottom: 4rem;
  width: calc(100% - 4rem);
  margin: 0 auto;
  border-radius: 0.4rem;
  @include bp(md) {
    width: 90%;
    position: absolute;
    bottom: -2rem;
    right: 0;
  }
}
