.c-grid {
  padding: 5rem 0;

  & > div > h2 {
    margin-top: 0;
    display: block;
    margin-bottom: 2rem;
  }

  &__inner {
    display: grid;
    gap: 1rem 2rem;
    grid-template-columns: minmax(0px, 1fr);
    @include bp(sm) {
      gap: 4rem 2rem;
    }
    &.carousel:after {
      content: 'flickity';
      display: none;
      @include bp(sm) {
        content: '';
      }
    }
  }

  &__1x {
    .o-container {
      display: flex;
      flex-direction: column;
      align-items: center;
    }
    & .c-grid__inner {
      grid-template-columns: minmax(0px, 1fr);
    }
  }
  &__2x {
    & .c-grid__inner {
      @include bp(md) {
        grid-template-columns: repeat(2, minmax(0px, 1fr));
      }
    }
  }
  &__3x {
    & .c-grid__inner {
      @include bp(sm) {
        grid-template-columns: repeat(2, minmax(0px, 1fr));
      }
      @include bp(md) {
        grid-template-columns: repeat(3, minmax(0px, 1fr));
      }
    }
  }
  &__4x {
    & .c-grid__inner {
      @include bp(sm) {
        grid-template-columns: repeat(2, minmax(0px, 1fr));
      }
      @include bp(md) {
        grid-template-columns: repeat(4, minmax(0px, 1fr));
      }
      // @include bp(lg) {
      //   grid-template-columns: repeat(4, minmax(0px, 1fr));
      // }
    }
  }

  // &__3x.c-grid--card,
  // &__1x.c-grid--card {
  //   padding: 0;
  //   & > div {
  //     transform: translateY(-4rem);
  //   }
  // }

  &__cta {
    display: inline-block;
  }
}
