/********************
** Typography
********************/

@include headers() {
  display: block;
  font-weight: normal;
  color: color('navy');
  font-family: $f-title;
}

.f-title--xl,
.f-title--lg,
.f-title--md,
.f-title--sm,
h2,
h3,
h4,
h5 {
  margin: 2rem 0 1rem;
}

h1,
.f-title--xxl {
  @include font-size(38);
  line-height: 1.26;
  margin: 2rem 0;
}

h2,
.f-title--xl {
  @include font-size(28);
  line-height: 1.28;
}

h3,
.f-title--lg {
  @include font-size(22);
  line-height: 1.27;
}

h4,
.f-title--md {
  @include font-size(18);
  line-height: 1.2;
}

h5,
.f-title--sm {
  @include font-size(16);
  line-height: 1.125;
}

a {
  text-decoration: none;
  color: color('cta-blue');
  font-weight: 500;
  transition: color 400ms $cubic-bezier, background-size 0.3s;
  background-repeat: no-repeat;
  background-size: 0% 100%;
  background-image: linear-gradient(
    transparent 85%,
    currentcolor 85%,
    currentcolor 92%,
    transparent 92%,
    transparent 100%
  );
  &:visited {
    color: color('cta-blue');
  }
  &:hover,
  &:focus {
    color: lighten(color('cta-blue'), 20%);
    background-size: 100% 100%;
  }
}

.f-text,
p {
  display: inline-block;
  margin: 1rem 0;
  line-height: 1.5;
}

.f-text--caption {
  @include font-size(14);
  line-height: 1.1;
  color: color('grey', 4);
}

em {
  font-weight: inherit;
  font-style: italic;
}

strong {
  font-style: bold;
}

p + p {
  margin-top: 1rem;
}

dl,
ol,
ul {
  display: inline-block;
  margin-bottom: 1rem;
}

ul,
ol {
  margin: 1rem 0 1rem 2rem;
  padding: 0 0;
}

li ul,
li ol {
  margin: 0 0 0 0;
}

ul {
  list-style-type: none;
}

ul li::before {
  content: '•';
  color: color('sky');
  display: inline-block;
  width: 1rem;
  margin-left: -1rem;
}
ul li {
  &.f-li--s-check::before {
    content: url('../img/ico_s_check.svg');
    line-height: 0;
    display: inline-block;
    vertical-align: middle;
    width: 3.5rem;
    margin-left: -3.5rem;
  }
  &.f-li--s-cross::before {
    content: url('../img/ico_s_x.svg');
    line-height: 0;
    display: inline-block;
    vertical-align: middle;
    width: 3.5rem;
    margin-left: -3.5rem;
  }

  &.f-li--g-check::before {
    content: url('../img/ico_g_check.svg');
    line-height: 0;
    display: inline-block;
    vertical-align: middle;
    width: 1.5rem;
    margin-right: 0.5rem;
    margin-left: -2rem;
  }
  &.f-li--g-cross::before {
    content: url('../img/ico_g_x.svg');
    line-height: 0;
    display: inline-block;
    vertical-align: middle;
    width: 1.5rem;
    margin-right: 0.5rem;
    margin-left: -2rem;
  }
  &.f-li--g-arrow::before {
    content: url('../img/bullet-arrow.svg');
    display: inline-block;
    vertical-align: middle;
    width: 1.25rem;
    margin-right: 0.5rem;
    margin-left: -1.75rem;
    line-height: 0;
  }
}

blockquote.wp-block-quote,
blockquote.f-text--bq,
.f-text--bq {
  position: relative;
  display: block;
  padding-left: 2rem;
  margin: 1rem 0 1rem 4rem;
  border-left: 0.4rem solid color('sky');
}

figure.wp-block-pullquote blockquote,
blockquote.f-text--pq,
.f-text--pq {
  position: relative;
  display: block;
  text-align: center;
  max-width: 82.8rem;
  margin: 0 auto;
}

figure.wp-block-pullquote blockquote p,
.f-text--pq__quote {
  display: block;
  font-size: 2.2rem;
  color: color('sky');
}

figure.wp-block-pullquote blockquote cite,
.f-text--pq__by-line {
  display: inline-block;
  font-weight: 500;
  font-style: normal;
  color: color('grey', 5);
}

.f-text--i {
  font-style: italic;
}

.f-text--i {
  font-style: normal;
}

.f-text--400 {
  font-weight: 400;
}

.f-text--500 {
  font-weight: 500;
}

.f-text--600 {
  font-weight: 600;
}
