.c-pagination {
  text-align: center;
  padding: 8rem 0;
  ul.pagination {
    margin: 0 -2rem;
  }

  li {
    margin: 0 1rem;
    display: inline-block;
  }
  li:before {
    content: '';
    display: none;
  }
  .page-number {
    color: color('cta-blue');
  }
  .page-number.current {
    font-weight: 600;
    color: color('sky');
    position: relative;
    &:after {
      content: '';
      position: absolute;
      width: 100%;
      height: 0.2rem;
      background-color: color('sky');
      bottom: -0.2rem;
      left: 0;
    }
  }
  .dots {
    color: color('grey', 4);
  }
}
