//make the footer and the sticky and
.o-container {
  max-width: 1280px;
  margin: 0 auto;
  padding-left: 2rem;
  padding-right: 2rem;
}

.o-container--flex {
  &-row {
    display: flex;
    flex-flow: row;
  }
  &-col {
    display: flex;
    flex-flow: column;
  }
  &__no-wrap {
    flex-wrap: nowrap;
  }
  &__item {
    &__grow-0 {
      flex-grow: 0;
    }
    &__grow-1 {
      flex-grow: 1;
    }
  }
}

//position
.u-p--stick {
  position: sticky;
  top: 0;
}

.u-p--abs {
  position: absolute;
}

.u-p--rel {
  position: relative;
}

.u-p--fix {
  position: fixed;
}

.u-p--stat {
  position: static;
}

.u-desktop-only {
  @include bp-max(md) {
    display: none;
    visibility: hidden;
  }
}

.u-mobile-only {
  @include bp(md) {
    display: none;
    visibility: hidden;
  }
}
