.c-bra {
  position: relative;
  margin-top: 2rem;
  &--dark &__inner {
    padding: 2rem 1rem;
    text-align: center;
    @include headers {
      color: white;
    }
    p {
      color: white;
    }
  }
  &__image {
    position: absolute;
    width: auto;
    height: 100%;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    overflow: hidden;
    svg {
      position: absolute;
      display: inline;
      height: 100%;
      min-width: 100%;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      @include bp(md) {
        min-width: initial;
        height: auto;
        width: 100%;
      }
    }
  }
  &__inner {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  &:hover {
    .c-btn--sky {
      background-color: lighten(color('sky'), 20%);
      color: color('white');
    }

    .c-btn--blue {
      background-color: lighten(color('cta-blue'), 20%);
      color: color('white');
    }

    .c-btn--green {
      background-color: lighten(color('cta-green'), 20%);
      color: color('white');
    }
  }
}
