//padding top and bottom
.u-s-ptb--xl {
  padding: 4rem 2rem;
}

.u-s-ptb--lg {
  padding: 2rem 2rem;
}

.u-s-ptb--md {
  padding: 1rem;
}

//padding control classes
.u-pt {
  &--none {
    padding-top: 0;
  }

  &--sm {
    padding-top: 2rem;
  }

  &--md {
    padding-top: 4rem;
  }

  &--lg {
    padding-top: 6rem;
  }
}

.u-pb {
  &--none {
    padding-bottom: 0;
  }

  &--sm {
    padding-bottom: 2rem;
  }

  &--md {
    padding-bottom: 4rem;
  }

  &--lg {
    padding-bottom: 6rem;
  }
}
