.c-lg {
  &__title {
    text-align: center;
    margin-top: 0;
    padding-top: 2rem;
  }
  &__logo {
    padding: 1rem;
    padding: 1rem 2rem;
    @include bp(md) {
      padding: 2rem 3rem;
    }
  }

  &__img {
    background-color: transparent !important;
  }
}

@include bp(md) {
  .u-watch {
    .c-lg__logo {
      transition-delay: var(--delay);
    }
  }
}
