/*
* Main Menu
*/
@include bp-max(md) {
  .header {
    .nav-main--wrapper {
      position: absolute;
      display: block;
      background-color: color('white');
      top: 100%;
      bottom: 0;
      left: 0;
      right: 0;
      height: 100%;
      min-height: calc(100vh - 6.041rem);
      margin: 0;
      padding: 4rem;
      transform-origin: center right;
      transform: scale3d(0, 1, 1);
      transition: transform 420ms ease-in-out 190ms;
    }

    .nav-main,
    .c-menu--main {
      position: relative;
      display: block;
      margin: 0;
    }

    .nav-btns {
      flex-direction: row-reverse;
    }
    .c-menu--main,
    .nav-btns {
      opacity: 0;
      transition: opacity 230ms ease-in-out;
      will-change: opacity;
    }
    &.open {
      .nav-main--wrapper {
        transform: scale3d(1, 1, 1);
        transition: transform 420ms ease-in-out;
      }
      .c-menu--main,
      .nav-btns {
        opacity: 1;
        transition: opacity 230ms ease-in-out 320ms;
      }
    }
  }

  .c-menu--main {
    & > li.menu-item:not(:first-of-type) {
      border-top: 0.1rem solid color('grey', 2);
    }
    & > li.menu-item {
      width: 100%;
      padding: 1rem 0;
      svg {
        display: inline-block;
        height: 1.2rem;
        width: 1.2rem;
        margin: 0 1rem;
        color: color('cta-blue');
        transform: rotate(0deg);
        vertical-align: middle;
        transition: transform 230ms ease;
      }
      &.menu-item-has-children {
        .c-menu__dd {
          max-height: 0rem;
          overflow: hidden;
          transition: max-height 230ms ease;
        }
      }
      &.open {
        &.menu-item-has-children {
          .c-menu__dd {
            max-height: var(--expand-to-height);
          }
        }
        & .i-chevron-down {
          transform: rotate(180deg);
        }
      }
    }
  }
}
.nav-main--wrapper {
  @include bp(md) {
    padding-left: 2rem;
  }
}
.c-menu--main {
  list-style: none;

  &__icon,
  &__icon img {
    width: 2.5rem;
    height: 2.5rem;
  }
  & > li.menu-item.menu-item-has-children > a {
    cursor: default;
  }

  & li {
    transition-duration: 0.5s;
  }

  @include bp(md) {
    & > li > a {
      white-space: nowrap;
    }
  }

  li .c-menu__dd {
    width: 100%;
    @include bp(md) {
      display: inline-block;
      width: initial;
      visibility: hidden;
      opacity: 0;
      position: absolute;
      z-index: 1;
      transition: opacity 0.5s ease-in-out, transform 0.5s ease-in-out;
      left: 0;
      top: 100%;
      display: none;
      transform: translate(0, 0);
      padding: 2rem;
      border-radius: 0.4rem;
      box-shadow: 0px 0px 8px #3f4f5f14;
    }
  }

  & li:hover > .c-menu__dd,
  & li:focus-within > .c-menu__dd,
  & li .c-menu__dd:hover {
    @include bp(md) {
      visibility: visible;
      opacity: 1;
      display: block;
    }
  }

  li ul li {
    width: 100%;
    display: inline-flex;
    align-items: center;
    > span {
      margin-right: 1rem;
    }
    @include bp(md) {
      width: max-content;
      display: flex;
      &:not(:last-of-type) {
        margin-bottom: 1rem;
      }
    }
  }
}

.c-menu--main li::before {
  display: none;
}

.c-menu--main > .menu-item {
  @include bp(md) {
    padding: 0 2rem;
  }

  .c-menu--main__icon {
    color: color('midnight');
    transition: color 230ms ease-in-out;
  }
  & ul .menu-item:hover .c-menu--main__icon {
    color: color('cta-blue') !important;
  }

  & a,
  & a:visited {
    color: color('midnight');
    position: relative;
    padding: 0.5rem 0;
    &:hover,
    &:focus {
      color: color('cta-blue');
      background-size: 100% 100%;
    }
    &:focus {
      outline: none;
    }
  }

  &.current-menu-ancestor > a {
    color: color('cta-blue') !important;
  }

  &.current-menu-item {
    & > .c-menu--main__icon,
    & > a {
      color: color('cta-blue') !important;
      background-size: 100% 100%;
    }
  }
}

/*
* Footer Menu Primary
*/
.c-menu--footer {
  & > p {
    font-weight: 500;
  }
}

.c-menu--footer__children {
  list-style-type: none;

  & li {
    padding: 0;
  }

  & li::before {
    display: none;
  }

  & li a {
    font-weight: 400;
  }
}

/*
* Footer Menu Secondary
*/
.c-menu--footer-secondary {
  li:before {
    display: none;
  }

  li:not(:last-of-type) {
    margin-right: 4rem;
  }
}
