.c-tabs {
  border-bottom: 0.1rem solid color('grey', 2);
  width: 100%;
  margin: 0 0;
  a#{&}__tab {
    font-size: 1.7rem;
    color: color('grey', 4);
    position: relative;
    padding: 1.2rem 0;
    margin: 0 3rem;
    background-size: 0;
    cursor: pointer;
    &:after {
      content: '';
      position: absolute;
      display: block;
      width: 100%;
      height: 0.2rem;
      background-color: color('cta-blue');
      bottom: 0;
      left: 50%;
      transform: translateX(-50%) scale(0);
      transition: transform 230ms ease-in-out;
    }

    &:focus {
      background-size: 0;
    }

    &:hover {
      color: color('cta-blue');
      background-size: 0;
      &:after {
        transform: translateX(-50%) scale(1);
      }
    }
  }

  a.c-tab--active:after {
    transform: translateX(-50%) scale(1);
  }

  a.c-tab--active {
    color: color('cta-blue');
  }
}
