.c-kc {
  padding: 6rem 2rem;
  @include bp(md) {
    display: grid;
    gap: 4rem;
    grid-template-columns: repeat(2, minmax(0px, 1fr));
  }

  &.no-bottom-padding {
    padding-bottom: 0;
  }

  > * {
    align-self: center;
  }

  &.has-form > * {
    align-self: start;
  }

  &--dark {
    label {
      color: white !important;
    }
  }

  &__content {
    ul,
    ol {
      li {
        margin: 1rem 0;
        &:first-child {
          margin-top: 0;
        }
        &:last-child {
          margin-bottom: 0;
        }
      }
    }
    &.add-box {
      background-color: color('white');
      border-radius: 0.4rem;
      padding: 2rem 1rem 3rem 1rem;
      @include bp(md) {
        padding: 2rem;
      }
    }
    @include bp(md) {
      padding: 2rem;
    }
    .c-block--content.u-c-white,
    &.add-box .c-block--content {
      & h1,
      & h2,
      & h3,
      & h4,
      & h5 {
        a {
          color: color('sky');
        }
      }
    }
  }

  @include bp(md) {
    &__content,
    &__media {
      grid-row: 1;
    }
  }

  &--center {
    grid-template-columns: minmax(0px, 1fr);
    .c-kc__content {
      text-align: center;
      grid-column: 1;
      ul,
      ol {
        display: block;
      }
    }
    .c-cta {
      justify-content: center;
    }
    @include bp(md) {
      grid-template-columns: minmax(0px, 1fr) 66.66666667% minmax(0px, 1fr);
      .c-kc__content {
        grid-column: 2;
      }
    }
    .c-kc__media {
      &--video,
      &--form {
        grid-column: 2;
        grid-row: 2;
      }
      &--image {
        position: absolute;
        width: 100%;
        height: 100%;
        max-width: 128rem;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        overflow: hidden;
        svg {
          position: absolute;
          display: inline;
          height: 100%;
          min-width: 100%;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
          @include bp(md) {
            min-width: initial;
            height: auto;
            width: 100%;
          }
        }
      }
    }
  }

  &--left {
    @include bp(md) {
      .c-kc__content {
        grid-column: 1;
      }
      .c-kc__media {
        grid-column: 2;
      }
    }
    &.has-form {
      @include bp(md) {
        .c-kc__content {
          grid-column: 2;
        }
        .c-kc__media {
          grid-column: 1;
        }
      }
    }
  }

  &--right {
    @include bp(md) {
      .c-kc__content {
        grid-column: 2;
      }
      .c-kc__media {
        grid-column: 1;
      }
    }
    &.has-form {
      @include bp(md) {
        .c-kc__content {
          grid-column: 1;
        }
        .c-kc__media {
          grid-column: 2;
        }
      }
    }
  }
}
