.o-careers {
  &__header {
    text-align: center;
    border-bottom: 0.1rem solid color('grey', 2);
    padding-bottom: 1rem;
    margin-bottom: 1rem;
  }

  &--wrapper {
    margin-bottom: 9rem;
  }

  &--content {
    margin-bottom: 2rem;
    li {
      margin: 2rem 0;
      &:first-child {
        margin-top: 0;
      }
      &:last-child {
        margin-bottom: 0;
      }
    }
  }
}
