.c-headshot-grid {
  display: grid;
  gap: 3rem 5.6rem;
  grid-template-columns: repeat(2, minmax(0px, 1fr));
  margin: 2rem auto 6rem;
  @include bp(sm) {
    grid-template-columns: repeat(2, minmax(0px, 1fr));
  }
  @include bp(md) {
    grid-template-columns: repeat(4, minmax(0px, 1fr));
  }
  @include bp(lg) {
    grid-template-columns: repeat(4, minmax(0px, 1fr));
  }
  .c-block-outer {
    padding: 0;
    align-self: center;
  }
  .c-block {
    margin: 0;
    &--content {
      padding: 0;
    }
  }
}
