* {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

*,
*:after,
*:before {
  -moz-box-sizing: border-box;
  -ms-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

*:hover {
}

html {
  font-size: 62.5%;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  height: 100%;
  min-height: 100vh;
  scroll-behavior: smooth;
}

body {
  line-height: $baseLineHeight;
  font-family: $f-base;
  @include font-size($baseFontSize);
  color: $c-grey-5;
  background: $c-white;
  font-size: 1.6rem;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
  -ms-interpolation-mode: bicubic;
  -moz-user-drag: -moz-none;
  -webkit-user-drag: none;
  user-drag: none;
}

figure {
  position: relative;
  margin: 0 0;
}

select,
textarea,
input[type='text'],
input[type='password'],
input[type='email'],
input[type='submit'] {
  display: block;
  border-radius: 0;
  @include ui-appearance(none);
}
