.c-404-wrapper {
  position: relative;
  padding: 4rem 0 10rem 0;
  & > div {
    margin: 2rem;
  }
  @include bp(md) {
    padding: 8rem 0 10rem 0;
  }
  .c-block {
    margin: 0;
  }
}

.c-404--text {
  font-size: 20rem;
  line-height: 0;
  top: 85%;
  right: 2.5%;
  opacity: 1;
  mix-blend-mode: multiply;
}
