}

/* The slider */
.c-slider {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  transition: background-color 0.4s ease-in-out;
  border-radius: 3.4rem;
  cursor: pointer;
}
.c-slider:before {
  position: absolute;
  content: "";
  height: 1.8rem;
  width: 1.8rem;
  left: 0.2rem;
  bottom: 0.2rem;
  background-color: white;
  transition: transform 0.4s ease-in-out;
  border-radius: 50%;
}

