.c-showcase {
  display: block;
  position: relative;
  padding: 6rem 0 0 0;

  &__title {
    margin-top: 0;
    text-align: center;
  }

  .c-kc {
    @include bp-max(md) {
      padding-top: 0;
    }
  }

  .c-block--content ul {
    margin-left: 0;
    li {
      margin: 2rem 0;
    }
    & li::before {
      content: '▶';
      font-weight: bold;
      color: color('sky');
      display: inline-block;
      width: 1.4rem;
      font-size: 0.4rem;
      vertical-align: middle;
      margin-left: -1.4rem;
    }
  }

  &__slide {
    &.c-slide--active {
      opacity: 1;
      display: inline-block;
      visibility: visible;
    }

    &.c-slide--inactive {
      opacity: 0;
      display: none;
      visibility: hidden;
    }
  }
}
