.c-dropdown {
  position: relative;
  display: inline-block;
  z-index: 2;
  & .dropbtn {
    text-align: left;
    background-color: transparent;
    color: #47596b;
    font-size: 1.8rem;
    line-height: 3rem;
    border: none;
    cursor: pointer;
    padding: 0 !important;
    position: relative;
    padding-bottom: 0px !important;
    font-weight: 500;
    padding-right: 2.2rem !important;
    &:after {
      content: '';
      display: block;
      position: absolute;
      width: 100%;
      height: 0.1rem;
      bottom: 0;
      border-bottom: 0.1rem dashed #91a3b0;
    }
    &.solid {
      margin-bottom: 2rem;
      line-height: 4rem;
    }
    &.solid.dropbtn {
      color: color('cta-blue');
      font-weight: 600;
    }
    &.solid:after {
      border-bottom: 0.1rem solid color('cta-blue');
    }
    &.small {
      font-size: 1.6rem;
      letter-spacing: 0;
      line-height: 3rem;
    }
    &:focus {
      outline: 0;
    }
  }
  & .dropdown-content {
    opacity: 0;
    visibility: hidden;
    position: absolute;
    background-color: white;
    min-width: 16.1rem;
    z-index: 1;
    padding: 0.4rem 0;
    border-radius: 0.4rem;
    box-shadow: 0 0 0.5rem 0 #e2eaf0;
    transition: opacity 230ms ease-in-out;
    top: 100%;
    left: 0;
  }

  & .dropdown-content a {
    color: #5b6b7c;
    padding: 0.6rem 1.2rem;
    text-decoration: none;
    display: block;
    cursor: pointer;
  }

  & .dropdown-content a:hover {
    background-color: #eff4f8;
  }

  & .dropdown-content a {
    &:hover,
    &:focus {
      background-size: 0;
    }
  }

  &.open .dropdown-content {
    opacity: 1;
    visibility: visible;
  }

  & .toggle-down {
    position: absolute;
    top: 1.2rem;
    right: 0rem;
    width: 1.2rem;
    height: 0.8rem;
    fill: color('cta-blue');
    display: inline;
    transform-origin: center center;
    transform: rotate(0deg);
    transition: transform 230ms ease-in-out;
  }

  & .dropbtn.solid .toggle-down {
    top: 1.6rem;
  }

  & .dropbtn.solid ~ .dropdown-content {
    top: calc(100% - 2rem);
    min-width: 28rem;
    left: 50%;
    transform: translate(-50%, 0%);
  }

  &.open .toggle-down {
    transform: rotate(180deg);
  }
}
