%c-ic-base {
  border-radius: 0.4rem;
  padding: 2rem;
  margin-top: 2rem;
  margin-top: 0;
  width: 100%;
  display: block;
  @include headers() {
    margin-top: 0rem;
  }
}

.c-ic {
  &-1 {
    @extend %c-ic-base;
    background-color: color('grey', 1);
  }
  &-2 {
    @extend %c-ic-base;
    background-color: color('grey', 2);
  }
}
