.c-block,
.c-card,
.c-icon-block {
  display: inline-block;
  margin: 1rem 2rem;

  .c-cta {
    margin-top: 1rem;
  }
  ul,
  ol {
    li {
      margin: 1rem 0;
      &:first-child {
        margin-top: 0;
      }
      &:last-child {
        margin-bottom: 0;
      }
    }
  }
}

.c-block,
.c-icon-block {
  .c-block--content {
    padding: 0 1rem 1rem;
  }
}

.c-card {
  overflow: hidden;
  background-color: color('white');
  border-radius: 0.4rem;
  box-shadow: 0px 3px 8px #3f4f5f15;
  transform: translate3d(0, 0, 0) scale(1);
  transition: transform 230ms ease-in-out;
  will-change: transform;
  &:hover {
    transform: translate3d(0, -0.5rem, 0) scale(1.015);
  }
  .c-card--content {
    padding: 1.25rem 2rem 2rem;
  }

  & h4,
  & h3,
  & h2,
  & h5 {
    transition: color 230ms ease-in-out;
  }

  & a + h4,
  & a + a h4,
  & a + h3,
  & a + a h3,
  & a + h2,
  & a + a h2,
  & a + h5,
  & a + a h5 {
    margin-top: 0.5rem;
  }

  &:hover h4,
  &:hover h3,
  &:hover h2,
  &:hover h5 {
    color: lighten(color('navy'), 20%);
  }
}

.c-icon-block {
  figure {
    width: auto;
    max-width: 9rem;
  }
}

.c-block {
  &.is-svg {
    display: inline-flex;
    align-content: top;
    @include bp(md) {
      display: inline-block;
    }

    figure {
      flex: 0 1 20%;
      svg {
        min-width: 100%;
      }
    }
    .c-block--content {
      flex: 1;
      & > *:first-child {
        @include bp-max(md) {
          margin-top: 0;
        }
      }
    }
  }
  .c-ar-img__arb {
    padding: 0.5rem;
    @include bp(md) {
      max-width: 30%;
    }
  }

  &--content.u-c-white {
    & h1,
    & h2,
    & h3,
    & h4,
    & h5 {
      color: color('white');
    }
  }
}
