.c-icon-content {
  &__inner {
    flex-wrap: nowrap;
  }

  &__img {
    flex: 0 0 auto;
    padding: 1rem;
    svg,
    img {
      height: 7.2rem;
    }
  }

  &__content {
    flex: 0 1 auto;
    h4 {
      margin: 0;
      padding: 1rem;
    }

    &__caption {
      @extend .f-text--caption;
      padding: 0 1rem 1rem 1rem;
      &--light {
        color: color('grey', 5);
      }
      &--dark {
        color: color('white');
      }
    }
    p {
      margin: 0;
    }
  }
}
