.c-logo {
  max-width: 16.6rem;
  width: 100%;
  height: 100%;
  @include bp(md) {
    max-width: 23.3rem;
  }
}

.c-logo--wrap--inline-flex {
  display: inline-flex;
  &,
  &:active,
  &:hover,
  &:focus {
    background-size: 0;
  }
}

.c-logo--mark {
  max-width: 3.4rem;
  width: 100%;
  @include bp(md) {
    max-width: 4rem;
  }
}
