.c-cta {
  display: grid;
  gap: 2rem;
  grid-auto-columns: max-content;
  margin-top: 2rem;
  .c-btn {
    align-self: center;
    grid-column: 1;
    grid-row: 1;
  }

  .c-inline-link {
    align-self: center;
    grid-column: auto;
    grid-row: 1;
  }

  &__sub {
    align-self: center;
    grid-column: 1 / span 2;
    grid-row: 2;
  }
}
