$ease-out-back: cubic-bezier(0.34, 1.56, 0.64, 1);
$ease-in-expo: cubic-bezier(0.16, 1, 0.3, 1);
$ease-in-out-sine: cubic-bezier(0.37, 0, 0.63, 1);
.u-watch {
  .draw {
    transition: stroke-dashoffset 6.5s ease;
    stroke-dasharray: 3212;
    stroke-dashoffset: 3212;
  }
  .fade-in {
    opacity: 0;
    transition: opacity 880ms $cubic-bezier;
  }
  .fade-out {
    opacity: 1;
    transition: opacity 880ms $cubic-bezier;
  }
  .float {
    transform: translateY(0);
    opacity: 0;
    transition: opacity 880ms $cubic-bezier;
    animation: float 6s ease-in-out infinite;
  }
  .slide-in {
    &-right {
      opacity: 0;
      transition: opacity 880ms $cubic-bezier, transform 880ms $cubic-bezier;
      transform: translateX(3rem);
    }
    &-left {
      opacity: 0;
      transition: opacity 880ms $cubic-bezier, transform 880ms $cubic-bezier;
      transform: translateX(-3rem);
    }
    &-up {
      opacity: 0;
      transition: opacity 880ms $cubic-bezier, transform 880ms $ease-out-back;
      transform: translateY(3rem);
    }
    &-down {
      opacity: 0;
      transition: opacity 880ms $cubic-bezier, transform 880ms $ease-out-back;
      transform: translateY(-3rem);
    }
    &-right,
    &-left,
    &-up,
    &-down {
      will-change: transform;
    }
  }

  &--active {
    .slide-in {
      &-right,
      &-up,
      &-left,
      &-down {
        opacity: 1;
      }
      &-right,
      &-left {
        transform: translateX(0);
      }
      &-up,
      &-down {
        transform: translateY(0);
      }
    }
    .draw {
      stroke-dashoffset: 0;
    }
    .fade-in,
    .float {
      opacity: 1;
    }
    .fade-out {
      opacity: 0;
    }
  }
  @for $i from 1 through 20 {
    .ani-#{$i} {
      transition-delay: #{($i - 1) * (0.35 * 880)}ms;
      animation-delay: #{($i - 1) * (0.35 * 880)}ms;
    }
  }
}
