.c-ph {
  padding-top: 4rem;
  &__inner {
    display: grid;
    grid-template-areas:
      '. title .'
      '. switch .'
      '. cards .';
  }
  &__title {
    grid-area: title;
    justify-self: center;
    text-align: center;
  }

  &__switch {
    grid-area: switch;
    justify-self: center;
  }

  &__cards {
    grid-area: cards;
    display: grid;
    justify-self: center;
    @include bp(md) {
      grid-template-columns: repeat(var(--cards), minmax(0, 41.6rem));
    }
  }
}
