.hbst-form-wrapper {
  padding: 4rem 4rem 6rem;
}

.hbspt-form {
  form > div:not(:first-of-type),
  form > fieldset:not(:first-of-type),
  form > .hs_recaptcha {
    margin-top: 2rem;
  }

  fieldset {
    width: 100%;
    max-width: initial !important;
    input,
    textarea {
      width: 100% !important;
    }
  }

  .form-columns-2 {
    > div {
      width: 100% !important;
      float: initial !important;
    }
    > div:last-child {
      margin-top: 2rem;
    }
    @include bp(sm) {
      > div {
        width: 50% !important;
        float: left !important;
      }
      > div:first-child {
        padding-right: 1rem;
      }
      > div:last-child {
        margin-top: 0rem;
        padding-left: 1rem;
      }
    }
  }
  .hs-fieldtype-select .input {
    position: relative;
    &:after {
      content: '';
      background-image: url('../img/dropdown-arrow_1.png');
      display: inline-block;
      position: absolute;
      top: 50%;
      right: 0.5rem;
      height: 1.3rem;
      width: 2rem;
      background-size: contain;
      background-repeat: no-repeat;
      transform: translate(-50%, -50%);
      pointer-events: none;
    }
  }

  .input {
    width: 100%;

    input,
    select,
    textarea {
      width: 100%;
      min-width: 100%;
      padding: 1.4rem;
      line-height: 1;
      border-radius: 0.4rem;
      background-color: white !important;
      border: 0.1rem solid color('grey', 2);
    }
  }
  .inputs-list li::before {
    display: none;
  }

  .hs-form-booleancheckbox-display {
    display: inline-block;
    input {
      width: auto !important;
      min-width: auto !important;
      margin-right: 1rem;
    }
  }

  .actions input.hs-button {
    @extend .c-btn, .c-btn--blue;
    cursor: pointer;
  }

  & form.btn--green {
    .actions input.hs-button {
      @extend .c-btn--green;
    }
  }

  & form.btn--sky {
    .actions input.hs-button {
      @extend .c-btn--sky;
    }
  }
}
