.c-toc {
  top: 7.6rem;
  padding: 2rem;
  > div {
    width: 100%;
  }
  .c-by-line {
    padding: 0.5rem;
    margin-bottom: 2rem;
  }
  &__ul {
    width: 100%;
    border-top: 2px solid color('grey', 2);
    &__li {
      & + & {
        margin-top: 2rem;
      }

      &::before {
        content: '';
        display: none;
      }

      & > a {
        display: list-item;
        list-style-position: outside;
        background-size: 0;
        &:visited {
          color: inherit;
        }
        &:before {
          content: '—';
          color: inherit;
          transition: color 230ms ease-in-out;
          width: 1rem;
          padding-right: 0.5rem;
          margin-left: -1.5rem;
        }

        &:hover,
        &:hover:before {
          background-size: 0;
          color: $c-grey-5;
        }

        &:focus,
        &:focus:before {
          color: $c-sky;
        }
      }
    }
  }
}

.c-toc {
  ul {
    padding: 2rem;
  }
}
