.c-demo-grid {
  position: relative;
  z-index: 0;
  margin-bottom: 6rem;
  &:before {
    position: absolute;
    content: '';
    width: 100%;
    top: 0;
    left: 0;
    background-color: var(--bg-color);
    height: 20%;
    @include bp(md) {
      height: 60%;
    }
  }
  &__inner {
    position: relative;
    z-index: 1;
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(1, minmax(0, 1fr));
    grid-template-areas:
      'cal'
      'cal'
      'g2'
      'tes';
    @include bp(md) {
      grid-template-columns: repeat(5, minmax(0, 1fr));
      grid-template-areas:
        'cal cal cal cal cal '
        'cal cal cal cal cal '
        '.   g2  tes tes .   ';
    }
  }

  &__cal {
    z-index: 1;
    grid-area: cal;
  }

  &__tes,
  &__g2 {
    @include bp(md) {
      padding: 0 4rem;
    }
  }

  &__g2 {
    img {
      margin: 0 auto;
    }
  }

  &__tes {
    z-index: 1;
    grid-area: tes;
    & > div {
      margin: 0;
    }
  }

  &__g2 {
    z-index: 1;
    display: flex;
    flex-flow: column;
    justify-content: center;
    align-content: middle;
    text-align: center;
    grid-area: g2;
    a {
      width: auto;
    }
    & a,
    & a:visited {
      color: color('sky');
    }
  }
}

//deal with calendly nonsense
// this just creates more issues i dont know what to do
.calendly-inline-widget {
  min-width: 100% !important;
  height: 82rem !important;
  @include bp(md) {
    height: 70rem !important;
  }
}
