.c-color-block {
  &__inner {
    display: grid;
    padding: 6rem 2rem;
    gap: 0rem 4rem;
    h2 {
      margin-top: 0;
      text-align: center;
      @include bp(md) {
        text-align: left;
      }
    }

    @include bp(md) {
      grid-template-columns: repeat(auto-fit, minmax(0px, 1fr));
    }
  }

  &__item,
  &__title {
    grid-row: auto;
    @include bp(md) {
      grid-row: 1;
    }
  }
  &__title {
    align-self: center;
    text-align: center;
    @include bp(md) {
      text-align: left;
    }
  }
  &__item {
    align-self: start;
    text-align: center;
    padding: 2rem;
    .c-ar-img {
      max-width: 9rem;
      margin: 1rem auto;
    }
  }
}
