footer {
  .foot__container {
    display: grid;
    gap: 2rem;
    grid-template-columns: 1fr 1fr;
    .foot__item--first {
      grid-column: 1 / span 2;
    }
    @include bp(md) {
      .foot__item--first {
        grid-column: 1;
      }
      gap: 2rem 8rem;
      grid-template-columns: minmax(0, 16.66666667%) repeat(
          5,
          minmax(0, max-content)
        );
    }
  }
  a,
  a:hover {
    animation: none;
    background-size: 0;
  }

  .footer__social-link {
    display: inline-block;
    line-height: 0;
    svg {
      height: 2rem;
      width: 2rem;
    }
    &:not(:first-child) {
      margin-left: 2rem;
    }
  }
}
